import Vue from 'vue';
import axios from 'axios';
import VueAxios from 'vue-axios';
Vue.use(VueAxios, axios);

//
import {
	cbs,
	gbs
} from './settings.js';


//
Vue.axios.defaults.baseURL = gbs.host;


/**
 * axios
 * @param  {string}   type      getpost
 * @param  {string}   url       URL
 * @param  {object}   data      
 * @param  {Function} fn        
 * @param  {boolean}   tokenFlag tokentruefalse
 */
var ajax = function(type, url, data, fn, tokenFlag) {

	this.$store.dispatch('show_loading');

	if (!tokenFlag) {
		data.token = this.$store.state.user.userinfo.token;
	}
	if (type === 'get') {
		var datas = {
			params: data
		};
	} else {
		var datas = data;
	}

	Vue.axios[type](url, datas).then((res) => {
		if (res.data.status === 200) {
			fn(res.data.data);
		} else {
			//
			cbs.statusError.call(this, res.data);
		}
		this.$store.dispatch('hide_loading');
	}).catch((err) => {
		// console.log(err);
		//

		this.$store.dispatch('hide_loading');
		cbs.requestError.call(this, err);
	});
};


/**
 * 
 * 
 * 
 * @type {Object}
 */
module.exports = {
	//
	user: {
		/**
		 * 
		 * @param {object} data 
		 * @param {string} data.username 
		 * @param {string} data.password 
		 * @param {function} fn 
		 */
		login(data, fn) {
			ajax.call(this, 'post', '/Login/login', data, fn, true);
		},

		/**
		 * 
		 * @param  	{object}   	data 			
		 * @param 	{string} 	data.username 	-
		 * @param 	{string} 	data.email 		-
		 * @param  	{Function} 	fn   			
		 */
		selectUser(data, fn) {
			ajax.call(this, 'get', '/User/selectUser', data, fn);
		},

		/**
		 * 
		 * @param {object}   data 
		 * @param {string} data.id ID-
		 * @param {string} data.username 
		 * @param {string} data.email 
		 * @param {string} data.sex 
		 * @param {string} data.birthday 
		 * @param {string} data.address 
		 * @param {string} data.status 
		 * @param {function} fn   
		 */
		saveUser(data, fn) {
			ajax.call(this, 'post', '/User/saveUser', data, fn);
		},

		/**
		 * 
		 * @param  {object}   data 
		 * @param {string} data.id IDID
		 * @param  {Function} fn   
		 */
		deleteUser(data, fn) {
			ajax.call(this, 'post', '/User/deleteUser', data, fn);
		},

		/**
		 * 
		 * @param  {string}   id ID
		 * @param  {Function} fn 
		 */
		findUser(id, fn) {
			ajax.call(this, 'get', '/User/findUser', {
				id: id
			}, fn);
		},


		/**
		 * 
		 * @param  {object}   data 
		 * @param {string} data.old_password 
		 * @param {string} data.password 
		 * @param {string} data.password_confirm 
		 * @param  {Function} fn   
		 */
		updPass(data, fn) {
			ajax.call(this, 'post', '/User/updatePass', data, fn);
		},

		/**
		 * 
		 * @param  {object}   data 
		 * @param {string} data.id ID
		 * @param {string} data.login_style 12
		 * @param {string} data.disabled_update_pass ID
		 * @param  {Function} fn   
		 */
		accessUser(data, fn) {
			ajax.call(this, 'post', '/User/accessUser', data, fn);
		}
	},

	/**
	 * 
	 * @type {Object}
	 */
	article: {
		/**
		 * 
		 * @param  {object}   data 
		 * @param  {Function} fn   
		 */
		selectArticle(data, fn) {
			ajax.call(this, 'get', '/Article/selectArticle', data, fn);
		},

		/**
		 * 
		 * @param  {object}   data 
		 * @param  {Function} fn   
		 */
		saveArticle(data, fn) {
			ajax.call(this, 'post', '/Article/saveArticle', data, fn);
		},

		/**
		 * 
		 * @param  {object}   data 
		 * @param  {Function} fn   
		 */
		deleteArticle(data, fn) {
			ajax.call(this, 'post', '/Article/deleteArticle', data, fn);
		},

		/**
		 * 
		 * @param  {object}   data 
		 * @param  {Function} fn   
		 */
		findArticle(data, fn) {
			ajax.call(this, 'get', '/Article/findArticle', data, fn);
		},
	},


	/**
	 * 
	 * @type {Object}
	 */
	order: {
		/**
		 * 
		 * @param  {object}   data 
		 * @param  {Function} fn   
		 */
		statisOrder(data, fn) {
			ajax.call(this, 'get', '/Order/statisOrder', data, fn);
		},

		/**
		 * 
		 * @param  {object}   data 
		 * @param  {Function} fn   
		 */
		selectOrder(data, fn) {
			ajax.call(this, 'get', '/Order/selectOrder', data, fn);
		},

		/**
		 * 
		 * @param  {object}   data 
		 * @param  {Function} fn   
		 */
		saveOrder(data, fn) {
			ajax.call(this, 'post', '/Order/saveOrder', data, fn);
		},
	},

	/**
	 * 
	 * @type {Object}
	 */
	system: {
		/**
		 * 
		 * @param  {Function} fn 
		 */
		getSetting(fn) {
			ajax.call(this, 'get', '/System/getSetting', {}, fn);
		},

		/**
		 * 
		 * @param  {object}   data 
		 * @param  {Function} fn   
		 */
		updateSetting(data, fn) {
			ajax.call(this, 'post', '/System/updateSetting', data, fn);
		}
	}
};


